export default {
	data() {
		return {
			option: {},
			nowperson: '',
			foo: {},
			record: [],
			swiperList: [],
			cardCur: 0,
		}
	},
	methods: {
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		previewImage(url){
			let data = url.split(',');
			for (let i=0;i<data.length;i++) {
				data[i] = data[i].replace(/\\/g,'/');
			}
			console.log(data);
			var that = this;
			uni.previewImage({
				urls: data,
				current: data[0]
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.swiperList,
				current: e.currentTarget.dataset.url
			});
		},
		getExecutor() {
			var self = this
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Get_Executors',
				data: {
					No: self.option.No
				}
			}).then(data => {
				var [error,res] = data;
				self.nowperson = res.data;
			})
		},
		getInfo() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Details_Problems',
				data: {
					YHXH: self.option.YHXH,
					state: self.option.state,
				}
			}).then(data => {
				var [error,res] = data;
				if (res.data.length>2) {
					var data = JSON.parse(res.data)
					console.log(data);
					self.foo = data[0];
					if (self.foo.AQZK == 1) {
						self.foo.AQZK = '一般隐患'
					} else {
						self.foo.AQZK = '严重隐患'
					}
				}
			})
		},
		getRecord() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_Processing',
				data: {
					No: self.option.No,
				}
			}).then(data => {
				var [error,res] = data;
				if (res.data != '暂无数据') {
					console.log(JSON.parse(res.data));
					self.record = JSON.parse(res.data)
				}
			})
		},
		getImg() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_YHimg',
				data: {
					No: self.option.No
				}
			}).then(data => {
				var [error,res] = data;
				let aa = res.data[0].url;
				self.swiperList = aa.split(',');
				console.log(aa)
			})
		},
	},
	onLoad(option) {
		uni.showLoading({
			title: '加载中...',
			mask: true
		})
		this.option = option;
		console.log(this.option)
	},
	mounted() {
		if (this.option.state == '0') {
			this.getExecutor();
		}
		this.getImg();
		this.getInfo();
		this.getRecord();
		this.$nextTick(function(){
			uni.hideLoading()
		})
	}
}
